.item {
    display: flex;
    gap: 15px;
    color: inherit;
    margin-bottom: 15px;
}

.item_img {
    width: 250px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.item_description {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
